text-align: center;\r\n}\r\n.container {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  max-width: 1000px;\r\n  margin: 0 auto;\r\n}\r\n.grid {\r\n  display: grid;\r\n  grid-template: repeat(11, 40.5px) / repeat(11, 40.5px);\r\n  justify-items: center;\r\n  align-items: center;\r\n  .board {\r\n    grid-row: 2 / -1;\r\n    grid-column: 2 / -1;\r\n    display: grid;\r\n    grid-template: repeat(10, 40px) / repeat(10, 40px);\r\n    border: 3px solid hsl(201, 100%, 36%);\r\n    &.disabled {\r\n      pointer-events: none;\r\n    }\r\n    .cell {\r\n      border: 1px solid hsl(201, 100%, 86%);\r\n      &.hovered {\r\n        background: hsl(201, 100%, 46%);\r\n      }\r\n      &.shipped {\r\n        background: #999;\r\n      }\r\n      &.missed {\r\n        background: hsl(201, 100%, 46%) !important;\r\n      }\r\n      &.hitted {\r\n        background: hsl(5, 80%, 65%) !important;\r\n      }\r\n      &.sunked {\r\n        background: hsl(353, 86%, 54%) !important;\r\n      }\r\n      &.computer {\r\n        cursor: pointer;\r\n        &:hover {\r\n          background: hsl(127, 27%, 61%);\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .coord {\r\n    color: hsl(201, 100%, 36%);\r\n    user-select: none;\r\n  }\r\n}\r\n.name {\r\n  margin: 0.5rem 0 0 0;\r\n  font-size: 1.3rem;\r\n  transform: translateX(20px);\r\n}\r\n.button {\r\n  margin: 2rem 0 0 0;\r\n  padding: 0.6rem 1.7rem;\r\n  font-size: 1.8rem;\r\n  border: none;\r\n  border-radius: 10px;\r\n  color: #fff;\r\n  background: #0077b6;\r\n  box-shadow: 2px 2px 0 1px #023e8a;\r\n  transition: box-shadow 0.1s ease;\r\n  cursor: pointer;\r\n  text-align: center;\r\n  &:active {\r\n    box-shadow: 0.5px 0.5px 0 0.5px #023e8a;\r\n  }\r\n}\r\n.ships {\r\n  max-width: 400px;\r\n  padding: 0.5rem;\r\n  margin: 1.5rem auto;\r\n  border: 3px solid hsl(201, 100%, 46%);\r\n  .title {\r\n    font-size: 1.8rem;\r\n    color: #0077b6;\r\n    margin: 0 0 1rem 0;\r\n  }\r\n  &-container {\r\n    display: flex;\r\n    